import { Paper } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import { useParams } from "react-router";
import AppCard from "../../components/Card";
import { GlobalState } from "../../Context/Context";
import { getPets, getUser } from "../../lib/lib";

function UserContactPage() {
  const { setLoading, change } = useContext(GlobalState);
  const [person, setPerson] = useState(null);
  const [owned, setOwned] = useState(null);
  const params = useParams();
  const myUser = async () => {
    try {
      setLoading(true);
      const response = await getUser(params.id);
      console.log(response._id);
      let obj = {
        userID: response._id,
      };
      const pets = await getPets(obj, 0);
      setPerson(response);
      console.log("user", response);
      setOwned(pets.pet);
    } catch (err) {
      console.log(err);
    } finally {
      setLoading(false);
    }
  };
  console.log("person", owned);
  useEffect(() => {
    myUser();
  }, [change]);
  return (
    <Paper
      style={{
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        marginTop: 10,
        padding: 30,
        overflow: "hidden",
        borderRadius: 10,
        width: 350,
        margin: "auto",
        alignSelf: "center",
        opacity: ".8",
      }}
      el
    >
      {console.log(person)}
      {person ? (
        <>
          {" "}
          <div>
            {person.firstName} {person.lastName}
          </div>
          <div>{person.email}</div>
          <div>{person.phoneNumber}</div>
          <div>{person.role}</div>{" "}
          {owned && owned.length
            ? owned.map((pet) => {
                return (
                  <AppCard
                    key={pet._id}
                    id={pet._id}
                    name={pet.petName}
                    imageUrl={pet.picture}
                    bio={pet.bio}
                    breed={pet.breed}
                    adoptionStatus={pet.adoptionStatus}
                  />
                );
              })
            : null}
        </>
      ) : null}
    </Paper>
  );
}

export default UserContactPage;
