import { Button, Input } from "@mui/material";
import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import Message from "../../components/Message";
import { GlobalState } from "../../Context/Context";
import { serverURL } from "../../lib/lib";
import socket from "../../lib/web-socket";
function Chat() {
  const [message, setMessage] = useState("");
  const [messages, setMessages] = useState([]);
  const { user } = useContext(GlobalState);
  const sendMessageHandler = (e) => {
    e.preventDefault();
    let name = user ? user.firstName : "Guest";
    socket.emit("sendMessage", { name, message });
    setMessage("");
  };
  const getMessages = async () => {
    try {
      const response = await axios.get(`${serverURL}/messages`);
      setMessages(response.data.messages);
    } catch (err) {
      console.log(err.response);
    }
  };
  useEffect(() => {
    socket.on("newMessage", (data) => {
      setMessages((prevMessages) => [...prevMessages, data]);
    });
    getMessages();
    return () => socket.off("newMessage");
  }, []);
  return (
    <div
      style={{
        height: "90vh",
        zIndex: "1",
        background: "white",
        borderRadius: "5px",
        opacity: ".5",
        display: "flex",
        flexDirection: "column",
        flexGrow: 1,
      }}
    >
      <div style={{ flex: "1" }}>
        {messages.length
          ? messages.map((message) => {
              return <Message message={message.message} name={message.name} />;
            })
          : null}
      </div>
      <div
        style={{
          display: "flex",
          width: "100%",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <form onSubmit={sendMessageHandler}>
          <Input
            style={{ width: "80%" }}
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            placeholder="Send Us A Message!"
            required
          />
          <Button type={"submit"}>Send</Button>
        </form>
      </div>
    </div>
  );
}

export default Chat;
